import * as yup from 'yup';

export const taskFormSchema = yup.object({
  startDay: yup
    .string()
    .matches(/^\d{2}\.\d{2}\.\d{4}$/, {
      message: '    ..'
    })
    .required('  '),
  endDay: yup
    .string()
    .matches(/^\d{2}\.\d{2}\.\d{4}$/, {
      message: '    ..'
    })
    .required('  '),
  text: yup.string().required(' ')
});
